<!--  UI  -->
<template>
  <div style="font-size: 0.7rem" class="mb-6">
    <div class="d-flex align-center">
      <div style="font-weight: bolder">{{ review.mname }}</div>
      <div class="ml-2">{{ review.rdate.substr(0, 10) }}</div>
      <div class="ml-auto">
        <v-rating
          background-color="orange lighten-3"
          color="orange"
          :value="review.rrate"
          class="pa-0"
          dense
          :size="20"
          readonly
        ></v-rating>
      </div>
    </div>
    <div class="mb-2" style="color: #386bfc">
      {{ review.pname }}, {{ review.pcolor }}, {{ review.psize }}
    </div>
    <div class="mb-2">
      <div class="d-flex">
        <div style="font-weight: bolder; width: 40px"></div>
        <div>{{ rprice[review.rprice] }}</div>
      </div>
      <div class="d-flex">
        <div style="font-weight: bolder; width: 40px"></div>
        <div>{{ rcomfortable[review.rcomfortable] }}</div>
      </div>
    </div>
    <div style="background-color: #eeeeee; border-radius: 4px" class="mt-1 pa-3">
      <div class="mb-1" style="font-weight: bolder">{{ review.rtitle }}</div>
      <div>
        {{ review.rcontent }}
      </div>
    </div>
    <div v-if="review.ranswer !== null" class="d-flex mt-2">
      <div>
        <v-icon>mdi-arrow-right-bottom-bold</v-icon>
      </div>
      <div style="background-color: #fcfaeb; border-radius: 4px; width: 100%">
        <div class="pa-3">
          <div style="font-weight: bolder"></div>
          <div>{{review.ranswer}}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  // component  (devtools  )
  name: "",
  //    
  components: {},
  //   
  data: function () {
    return {
      rprice: ["", "", ""],
      rcomfortable: ["", "", ""],
    };
  },
  //   
  methods: {},
  props: { review: Object },
  created() {},
};
</script>

